<template>
  <div class="co-brands" :class="{'empty':showEmpty}">
      <div v-if="showEmpty" class="div-empty">
        <img onclick="return false" class="img" src="../../../assets/aml_logo.png"/>
        <p class="empty-tip">敬请期待~</p> 
      </div>
      <ul v-else v-infinite-scroll="getProjectData" infinite-scroll-disabled="cellLoading" infinite-scroll-distance="20" infinite-scroll-immediate-check="false">
          <li v-for="item in brandData" :key="item.id">
            <project-item :data="item"></project-item>
          </li>
      </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
import projectItem from "./projectItem.vue";
import Vue from "vue";
import {InfiniteScroll } from "mint-ui";
Vue.use(InfiniteScroll);

export default {
  name: "co-brands",
  data() {
    return {
      brandData: [],
      isLoadDb: false,
      showEmpty: false,
      cellLoading: true,
      curPage: 0,
      pageNum: 10,
      existIdArr: [],
    };
  },
  components: { projectItem },
  activated() {
    document.body.scrollTop = 0;
    if (this.isLoadDb) this.$store.dispatch("toggleLoading", false);
  },
  created() {
    this.getProjectData(false);
  },
  beforeRouteLeave(to, from, next) {
    next();
  },
  methods: {
    getProjectData(infinite = true) {
      if(this.$route.name!='coBrands') return;   //防止无限加载在其它页面也能被监听
      this.$http
        .post(this.$store.state.path + "php/Route.php", {
          type: "goodsbytp",
          gtype: 21,
          ranktp: 0,
          lift: 0,
          page: this.curPage,
          userid: this.userid
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          if (infinite && this.curPage == 0) return;
          if (infinite && this.cellLoading) return;
          this.cellLoading = true;
          this.isLoadDb = true;

          let serverData = response.body.arr;
          if (!serverData || serverData.length == 0) {
            //数据为空
            this.cellLoading = true;
            if(this.curPage==0) this.showEmpty = true;
          } else {
            //有数据
            if (serverData.length % this.pageNum == 0) {
              //满一页数据
              this.curPage++;
              this.cellLoading = false;
            } else {
              //不满一页
              this.cellLoading = true;
            }
            for (let i = 0; i < serverData.length; i++) {
              let id = serverData[i].id;
              if (this.existIdArr.indexOf(id) >= 0) continue;
              this.brandData.push(serverData[i]);
              this.existIdArr.push(id);
            }
          }
        })
        .catch(response => {
          this.$store.dispatch("toggleLoading", false);
        });
    }
  },
  computed: {
    ...mapState(["openid", "path", "userid", "token"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.co-brands {
  width: 100%;
  flex 1;
  padding-bottom .07rem;

  &.empty{
    position: relative;
    display: flex;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
  }
  
  ul {
    padding: 0.1rem 0.1rem 0 0.1rem;

    li {
      width: 100%;
      margin-bottom: 0.05rem;
      box-shadow: 0 1px 0px rgba(150, 150, 150, 0.1);
    }
  }

  .div-empty {
    .img {
      width: 1.7rem;
      height: auto;
    }

    .empty-tip {
      margin-top: 0.12rem;
      font-size: 0.15rem;
      text-align: center;
      color: #a2733d;
      letter-spacing: 0.01rem;
    }
  }
}
</style>



